.MoviePageCard {
    display: grid;
    grid-template-rows:  0.07fr 0.07fr 0.07fr 1fr repeat(2, 0.05fr);
    grid-template-columns: repeat(4, 1fr);
    padding: 25px;
    border-style: solid;
    border-color: blue;
    border-radius: 2%;
    margin: 10px;
    place-self: center stretch;

    background-color: rgb(111, 202, 232);}

    .MoviePageCard .div1 { grid-area: 1 / 1 / 3 / 2; padding-left: 5px; padding-right: 5px;}
    .MoviePageCard .div2 { grid-area: 3 / 1 / 4 / 2; justify-self: right; padding-left: 5px; padding-right: 10px; padding-top: 5px;
    padding-bottom: 5px;}
    .MoviePageCard .div3 { grid-area: 1 / 2 / 2 / 5; justify-self: right;border-style: solid; padding-left: 5px; padding-right: 5px; padding-top: 5px;
        padding-bottom: 5px;}
    .MoviePageCard .div4 { grid-area: 2 / 2 / 4 / 5; border-style: solid; padding: 5px}
    .MoviePageCard .div5 { grid-area: 4 / 2 / 5 / 5; padding-left: 5px; padding-right: 5px;}
    .MoviePageCard .div6 { grid-area: 4 / 1 / 7 / 2; padding-left: 5px; padding-right: 5px; }
    .MoviePageCard .div7 { grid-area: 5 / 2 / 6 / 5; justify-self: right; padding-top: 5px;
        padding-bottom: 5px; padding-left: 5px; padding-right: 10px;}
    .MoviePageCard .div8 { grid-area: 6 / 2 / 7 / 5; justify-self: right; padding-top: 5px;
        padding-bottom: 0px; padding-left: 5px; padding-right: 10px;}
    .MoviePageCard .div9 { grid-area: 1 / 1 / 4 / 2; border-style: solid; padding: 5px}
    .MoviePageCard .div10 { grid-area: 4 / 2 / 7 / 5; border-style: solid; padding: 5px}
