.MetadataCard {
    display: grid;
    font-size: 10px;
    font-family: Arial, Helvetica, sans-serif;
    grid-template-rows: repeat(8, 1fr);
    grid-template-columns: repeat(3, 1fr);
    padding: 25px;
    border-style: solid;
    border-color: blue;
    border-radius: 2%;
    place-self: center stretch;
    margin-bottom: 20px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
   grid-auto-rows: 1px;
    background-color: rgb(111, 202, 232);}

.MetadataCard .button1:hover
{
    background-color: rgb(54, 114, 203); 
    border-radius: 12px;  
    padding: 5px;
}

.MetadataCard .button1
{
    background-color: rgb(51, 142, 207); 
    border-radius: 12px;  
    padding: 5px;
}


.MetadataCard .div1 { grid-area: 1 / 1 / 2 / 3; }
.MetadataCard .div2 { grid-area: 2 / 1 / 3 / 2; }
.MetadataCard .div3 { grid-area: 2 / 2 / 3 / 3; }
.MetadataCard .div4 { grid-area: 2 / 3 / 3 / 4; }
.MetadataCard .div5 { grid-area: 3 / 1 / 4 / 2; }
.MetadataCard .div6 { grid-area: 3 / 2 / 4 / 4; }
.MetadataCard .div7 { grid-area: 4 / 1 / 5 / 2; }
.MetadataCard .div8 { grid-area: 4 / 2 / 5 / 3; }
.MetadataCard .div9 { grid-area: 4 / 3 / 5 / 4; }
.MetadataCard .div10 { grid-area: 5 / 1 / 6 / 4; }
.MetadataCard .div11 { grid-area: 6 / 1 / 7 / 4; }
.MetadataCard .div12 { grid-area: 7 / 1 / 8 / 4; }
.MetadataCard .div13 { grid-area: 8 / 1 / 9 / 4; }
.MetadataCard .div14 { grid-area: 1 / 3 / 2 / 4; justify-self: right;}